import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from '../../common/constants/common.constant';
import { MdmConfigApiRoutes } from '../constants/mdmConfig.constant';

export interface FilterConfig {
  page: number;
  rowsPerPage: number;
}

export interface MdmConfigModel {
  device_id: string;
  device_name: string;
  application_id: string;
  application_name: string;
  package_name: string;
  version_id: string;
  version_name: string;
  version_code: number;
  is_active: boolean;
  message: string;
  area_code: string;
  updated_date: Date;
  logTime: Date;
}

export interface MdmChangeActive {
  device_id: string;
  application_id: string;
  is_active: boolean;
  area_code: string;
  message: string;
}

export class ConfigHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEVICE) {
    this.request = new HttpRequest(endPoint).request;
  }

  private configRequestBody = (mdmConfig: MdmConfigModel) => {
    let body = {
      device_id: mdmConfig.device_id,
      device_configs: [
        {
          application_id: mdmConfig.application_id,
          version_id: mdmConfig.version_id,
          actionCommand: 0,
          logTime: mdmConfig.logTime,
          message: mdmConfig.message,
        },
      ],
    };
    return body;
  };

  private configChangeActiveBody = (mdmChangeActive: MdmChangeActive) => {
    let body = {
      ids: [
        {
          application_id: mdmChangeActive.application_id,
          device_id: mdmChangeActive.device_id,
        },
      ],
      is_active: mdmChangeActive.is_active,
      area_code: mdmChangeActive.area_code,
      message: mdmChangeActive.message,
    };
    return body;
  };

  private configChangeActiveAreaBody = (
    mdmChangeActiveArea: MdmChangeActive,
  ) => {
    let body = {
      ids: [],
      is_active: mdmChangeActiveArea.is_active,
      area_code: mdmChangeActiveArea.area_code,
      message: mdmChangeActiveArea.message,
    };
    return body;
  };

  public getMdmConfigAll = (filter: any): Promise<any> => {
    return this.request.get(MdmConfigApiRoutes.GET_ALL_MDM_CONFIG, filter);
  };

  public getMdmDeviceConfigAll = (filter: any): Promise<any> => {
    return this.request.get(MdmConfigApiRoutes.GET_MDM_DEVICE_CONFIG, filter);
  };

  public getMdmDeviceByid = (id: any): Promise<any> => {
    return this.request.get(MdmConfigApiRoutes.GET_MDM_DEVICE_BY_ID + '/' + id);
  };

  public getMdmAppByid = (id: any): Promise<any> => {
    return this.request.get(MdmConfigApiRoutes.GET_MDM_APP_BY_ID + '/' + id);
  };

  public getMdmAppConfigAll = (filter: any): Promise<any> => {
    return this.request.get(MdmConfigApiRoutes.GET_MDM_APP_CONFIG, filter);
  };

  public getMdmConfigById = ({ deviceId, appId }: any): Promise<any> => {
    return this.request.get(
      MdmConfigApiRoutes.GET_BY_ID_MDM_CONFIG + '/' + deviceId + '/' + appId,
    );
  };

  public createMdmConfig = (mdmConfig: MdmConfigModel): Promise<any> => {
    return this.request.post(MdmConfigApiRoutes.CREATE_MDM_CONFIG, {
      ...this.configRequestBody(mdmConfig),
    });
  };

  public updateMdmConfig = (mdmConfig: any): Promise<any> => {
    return this.request.post(MdmConfigApiRoutes.UPDATE_MDM_CONFIG, mdmConfig);
  };

  public deleteMdmConfig = (dataDelete: any): Promise<any> => {
    return this.request.post(
      MdmConfigApiRoutes.DELETE_MDM_CONFIG +
        '/' +
        dataDelete.deviceId +
        '/' +
        dataDelete.appId,
    );
  };

  public changeActiveAreaMdmConfig = (
    mdmChangeActiveArea: any,
  ): Promise<any> => {
    return this.request.post(MdmConfigApiRoutes.CHANGE_ACTIVE, {
      ...this.configChangeActiveAreaBody(mdmChangeActiveArea),
    });
  };

  public changeActiveMdmConfig = (mdmChangeActive: any): Promise<any> => {
    return this.request.post(MdmConfigApiRoutes.CHANGE_ACTIVE, {
      ...this.configChangeActiveBody(mdmChangeActive),
    });
  };

  public resetLogMdmConfig = (mdmResetLog: any): Promise<any> => {
    return this.request.post(MdmConfigApiRoutes.RESET_LOG_COMMAND, {
      ...mdmResetLog,
      action_command: 3,
    });
  };

  public sendLogMdmConfig = (mdmSendLog: any): Promise<any> => {
    return this.request.post(MdmConfigApiRoutes.RESET_LOG_COMMAND, {
      ...mdmSendLog,
      action_command: 1,
    });
  };

  public getLogMdmConfig = (dataLog: any): Promise<any> => {
    return this.request.get(
      MdmConfigApiRoutes.GET_LOG_MDM_CONFIG +
        '/' +
        dataLog.device_id +
        '/' +
        dataLog.application_id,
    );
  };
}
