/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable array-callback-return */
import React, { useEffect, useState, useReducer, useMemo } from 'react';
import * as MdmVersionSelector from 'store/mdmVersion/shared/selectors';
import * as MdmVersionSlice from 'store/mdmVersion/shared/slice';
import * as _ from 'lodash';
import { useDispatch, useSelector } from 'react-redux';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useForm } from 'react-hook-form';
import AppLoading from 'app/components/Loading';
import { Order } from 'utils/app.helper';
import {
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { MdmVersionSaga } from 'store/mdmVersion/shared/saga';
import { Unsubscribe } from 'redux';
import { RootStore } from 'store/configureStore';
import TableHeaderSort from 'app/components/TableHeaderSort';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import { Box, Collapse, IconButton, Tooltip } from '@material-ui/core';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import { makeStyles } from '@material-ui/core';
import { MDMVersionModal } from '../components/MDMVersionModal';
import { ModalConfirm } from 'app/components/Modal/Confirm';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export const tableMdmVersion: any[] = [
  { id: 'collaps', label: '', minWidth: 120 },
  { id: 'code', label: 'Mã Phiên Bản', minWidth: 120 },
  { id: 'name', label: 'Tên Phiên Bản', minWidth: 120 },
  { id: 'application_name', label: 'Tên Ứng Dụng', minWidth: 120 },
  { id: 'actions', label: 'Thao Tác', minWidth: 120 },
];

export const tableMdmCollaps: any[] = [
  { id: 'is_active', label: '', minWidth: 120 },
  { id: 'name', label: '', minWidth: 120 },
  { id: 'link', label: '', minWidth: 120 },
  { id: 'release_note', label: '', minWidth: 120 },
];

interface ModalState {
  openModal: boolean;
  confirmDel: boolean;
  deleteModal: boolean;
}

enum ModalType {
  OPEN = 'OPEN',
  CONFIRM = 'CONFIRM',
  DELETE = 'DELETE',
  CLOSE_ALL = 'CLOSE_ALL',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  openModal: false,
  confirmDel: false,
  deleteModal: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN:
      return {
        ...state,
        openModal: payload,
      };
    case ModalType.CONFIRM:
      return {
        ...state,
        confirmDel: payload,
      };
    case ModalType.DELETE:
      return {
        ...state,
        deleteModal: payload,
      };
    case ModalType.CLOSE_ALL:
      return initModalState;
    default:
      return state;
  }
};

const defaultPagi = {
  take: 10,
  skip: 0,
};

export function MDMVersion() {
  const classes = useStyles();
  const dispatch = useDispatch();
  useInjectReducer({
    key: MdmVersionSlice.sliceKey,
    reducer: MdmVersionSlice.reducer,
  });
  useInjectSaga({ key: MdmVersionSlice.sliceKey, saga: MdmVersionSaga });
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const loading: any = useSelector<any>(MdmVersionSelector.selectLoading);
  const { handleSubmit, register, reset, getValues } = useForm();
  const [{ openModal, deleteModal }, dispatchModal] = useReducer(
    reduceModal,
    initModalState,
  );
  const error: any = useSelector<any>(MdmVersionSelector.selectError);
  const success: any = useSelector<any>(MdmVersionSelector.selectSuccess);
  const versions: any = useSelector<any>(MdmVersionSelector.selecMdmVersions);
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('name');
  const [versionId, setVersionId] = React.useState<any>(null);

  useEffect(() => {
    dispatch(MdmVersionSlice.actions.getMdmVersionAll(defaultPagi));
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case MdmVersionSlice.actions.getMdmVersionByIdSuccess.type:
          dispatchModal({
            type: ModalType.OPEN,
            payload: true,
          });
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(error)) {
      notifyController.setNotifyMessage(error.message).setFailNotify();
      dispatch(MdmVersionSlice.actions.clearError());
    }
  }, [error]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      dispatch(MdmVersionSlice.actions.clearSuccess());
      dispatch(MdmVersionSlice.actions.getMdmVersionAll(defaultPagi));
      setPagi(defaultPagi);
    }
  }, [success]);

  const submitSearch = (values: any) => {
    dispatch(
      MdmVersionSlice.actions.getMdmVersionAll({
        ...values,
        take: 10,
        skip: 0,
      }),
    );
  };

  const getFormValues = (): any => {
    return {
      ApplicationName: getValues('ApplicationName'),
    };
  };

  const renderData: any = useMemo(() => {
    const { data = [], total } = versions;
    const { skip, take } = pagi;
    return {
      data,
      total,
      skip,
      take,
    };
  }, [versions]);

  const renderTableControl = () => {
    return (
      <form onSubmit={handleSubmit(submitSearch)}>
        <div className="row" style={{ justifyContent: 'space-between' }}>
          <div className="col-md-2">
            <input
              type="text"
              placeholder="Tìm Theo Ứng Dụng"
              className="form-control form-control-sm"
              style={{ height: '37px' }}
              name="ApplicationName"
              ref={register()}
            />
          </div>
          <div className="col-md-5">
            <div className="btn-group">
              <button className="btn btn-info btn__config" type="submit">
                Tìm Kiếm
              </button>
            </div>
            <div className="btn-group ml-2">
              <button
                className="btn btn-info btn__config"
                type="reset"
                onClick={() => {
                  dispatch(
                    MdmVersionSlice.actions.getMdmVersionAll(defaultPagi),
                  );
                  setPagi(defaultPagi);
                }}
              >
                Reset Tìm Kiếm
              </button>
            </div>
          </div>
          <div className="col-md-5 text-right">
            <div className="btn-group">
              <button
                className="btn btn-primary btn__config"
                type="button"
                onClick={() => {
                  dispatchModal({
                    type: ModalType.OPEN,
                    payload: true,
                  });
                }}
              >
                Thêm Phiên Bản
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (e, page) => {
    let changePage = Object.assign({}, pagi, { skip: page * pagi.take });
    const request = {
      ...changePage,
      ...getFormValues(),
    };
    setPagi(changePage);
    dispatch(MdmVersionSlice.actions.getMdmVersionAll(request));
  };

  const handleChangeRowsPerPage = e => {
    let changeRowPerPage = Object.assign({}, pagi, {
      take: +e.target.value,
      skip: 0,
    });
    const request = {
      ...changeRowPerPage,
      ...getFormValues(),
    };
    setPagi(changeRowPerPage);
    dispatch(MdmVersionSlice.actions.getMdmVersionAll(request));
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tableMdmVersion}
      />
    );
  };

  const deleteVersion = id => {
    if (!_.isEmpty(id)) {
      setVersionId(id);
      dispatchModal({
        type: ModalType.DELETE,
        payload: true,
      });
    }
  };

  const handleDeleteVersion = () => {
    if (!_.isEmpty(versionId)) {
      dispatch(MdmVersionSlice.actions.deleteMdmVersion(versionId));
    }
  };

  const renderTable = () => {
    if (!_.isEmpty(renderData) || renderData.data.length) {
      return renderData.data.map((data, _id) => {
        return (
          <ConfigCollapse
            row={data}
            key={_id}
            events={{
              deleteVersion,
            }}
          />
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tableMdmVersion.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={renderData.total}
          rowsPerPage={renderData.take}
          page={renderData.skip / renderData.take}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading ? <AppLoading loading={true} /> : null}
      <div className="col-md-12">
        {openModal && (
          <MDMVersionModal
            open={openModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.OPEN,
                payload: false,
              });
            }}
          />
        )}
        <ModalConfirm
          title={'Bạn có chắc chắn muốn xoá thiết bị này?'}
          other={{
            bodyText: '',
          }}
          keyConfirm={''}
          open={deleteModal}
          onClose={() => {
            dispatchModal({
              type: ModalType.DELETE,
              payload: false,
            });
          }}
          confirmMethod={() => handleDeleteVersion()}
        />
        <div className="card card-topline-red">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

const useRowStyles = makeStyles({
  root: {
    '& > *': {
      backgroundColor: '#f1f0f0',
      borderBottom: 'unset',
    },
  },
});

const activeStyle = {
  icon: {
    active: {
      fontSize: '1rem',
      color: '#1ccc52',
    },
    deactive: {
      fontSize: '1rem',
      color: '#cc1c34',
    },
  },
};

export function ConfigCollapse({ row, events }) {
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();
  return (
    <React.Fragment>
      <TableRow>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell>{row.code}</TableCell>
        <TableCell>{row.name}</TableCell>
        <TableCell>{row.application_name}</TableCell>
        <TableCell>
          <Tooltip title={'Xóa'} enterDelay={300} leaveDelay={100}>
            <button
              className="btn btn-danger btn-xs btn--icon-text"
              onClick={() => {
                events.deleteVersion(row.id);
              }}
            >
              <i className="fa fa-times"></i>
            </button>
          </Tooltip>
        </TableCell>
      </TableRow>
      <TableRow className={classes.root}>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box>
              <h5 className="font-weight-bold mt-2"> Các file Tải Lên</h5>
              <Table size="small" aria-label="versions">
                <TableBody>
                  {row.bundles.map((bundle, _idx) => {
                    return (
                      <TableRow key={_idx}>
                        <TableCell style={{ width: '100px' }}>
                          {bundle.is_active ? (
                            <i
                              style={activeStyle.icon.active}
                              className="fa fa-check-circle"
                              aria-hidden="true"
                            ></i>
                          ) : (
                            <i
                              style={activeStyle.icon.deactive}
                              className="fa fa-times-circle"
                              aria-hidden="true"
                            ></i>
                          )}
                        </TableCell>
                        <TableCell style={{ width: '170px' }}>
                          {bundle.name}
                        </TableCell>
                        <TableCell style={{ width: '600px' }}>
                          <a
                            target="_blank"
                            href={bundle?.link}
                            rel="noopener noreferrer"
                          >
                            {bundle?.link}
                          </a>
                        </TableCell>
                        <TableCell>{bundle?.release_note}</TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}
