/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState, useReducer, useMemo } from 'react';
import * as MdmAppSelector from 'store/mdmApp/shared/selectors';
import * as MdmAppSlice from 'store/mdmApp/shared/slice';
import * as _ from 'lodash';
import { useDispatch, useSelector } from 'react-redux';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useForm } from 'react-hook-form';
import AppLoading from 'app/components/Loading';
import DateFnsUtils from '@date-io/date-fns';
import { AppHelper, Order } from 'utils/app.helper';
import {
  DEFAULT_FORMAT_DATE_TIME,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { MdmAppSaga } from 'store/mdmApp/shared/saga';
import { Unsubscribe } from 'redux';
import { RootStore } from 'store/configureStore';

import TableHeaderSort from 'app/components/TableHeaderSort';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import { Tooltip } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import { MDMModalApp } from '../components/MDMAppModal';
import { ModalConfirm } from 'app/components/Modal/Confirm';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export const tableMdmApp: any[] = [
  { id: 'name', label: 'Tên Ứng Dụng', minWidth: 120 },
  { id: 'package_name', label: 'Tên Gói', minWidth: 120 },
  { id: 'created_by', label: 'Tạo Bởi', minWidth: 120 },
  { id: 'created_date', label: 'Ngày Tạo', minWidth: 120 },
  { id: 'updated_by', label: 'Cập Nhật Bỏi', minWidth: 170 },
  { id: 'updated_date', label: 'Ngày Cập Nhật', minWidth: 170 },
  { id: 'actions', label: 'Chức năng', minWidth: 170 },
];

export const areas = [
  { code: 'B', label: 'Khu B' },
  { code: 'D', label: 'Khu D' },
];

interface ModalState {
  openModal: boolean;
  confirmDel: boolean;
  deleteModal: boolean;
}

enum ModalType {
  OPEN = 'OPEN',
  DELETE = 'DELETE',
  CONFIRM = 'CONFIRM',
  CLOSE_ALL = 'CLOSE_ALL',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  openModal: false,
  confirmDel: false,
  deleteModal: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN:
      return {
        ...state,
        openModal: payload,
      };
    case ModalType.CONFIRM:
      return {
        ...state,
        confirmDel: payload,
      };
    case ModalType.DELETE:
      return {
        ...state,
        deleteModal: payload,
      };
    case ModalType.CLOSE_ALL:
      return initModalState;
    default:
      return state;
  }
};

const defaultPagi = {
  take: 10,
  skip: 0,
};

export function MDMApp() {
  const classes = useStyles();
  const dispatch = useDispatch();
  useInjectReducer({
    key: MdmAppSlice.sliceKey,
    reducer: MdmAppSlice.reducer,
  });
  useInjectSaga({ key: MdmAppSlice.sliceKey, saga: MdmAppSaga });
  const { enqueueSnackbar } = useSnackbar();
  const dateUtil = new DateFnsUtils();
  const notifyController = new NotifyService(enqueueSnackbar);
  const error: any = useSelector<any>(MdmAppSelector.selectError);
  const success: any = useSelector<any>(MdmAppSelector.selectSuccess);
  const loading: any = useSelector<any>(MdmAppSelector.selectLoading);
  const apps: any = useSelector<any>(MdmAppSelector.selecMdmtApps);
  const [appId, setAppId] = useState<any>(null);
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [{ openModal, confirmDel, deleteModal }, dispatchModal] = useReducer(
    reduceModal,
    initModalState,
  );
  const { handleSubmit, register, reset, getValues } = useForm();
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('name');

  useEffect(() => {
    dispatch(MdmAppSlice.actions.getMdmAppAll(defaultPagi));
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case MdmAppSlice.actions.getMdmAppByIdSuccess.type:
          dispatchModal({
            type: ModalType.OPEN,
            payload: true,
          });
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(error)) {
      notifyController.setNotifyMessage(error.message).setFailNotify();
      dispatch(MdmAppSlice.actions.clearError());
    }
  }, [dispatch, error, notifyController]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      dispatch(MdmAppSlice.actions.clearSuccess());
      dispatch(MdmAppSlice.actions.getMdmAppAll(defaultPagi));
      setPagi(defaultPagi);
    }
  }, [success]);

  const submitSearch = (values: any) => {
    dispatch(
      MdmAppSlice.actions.getMdmAppAll({
        ...values,
        take: 10,
        skip: 0,
      }),
    );
  };

  const getFormValues = (): any => {
    return {
      Name: getValues('Name'),
      PackageName: getValues('PackageName'),
    };
  };

  const handleDeleteDevice = () => {
    dispatch(MdmAppSlice.actions.deleteMdmApp(appId));
  };

  const renderData: any = useMemo(() => {
    const { data = [], total } = apps;
    const { skip, take } = pagi;
    return {
      data,
      total,
      skip,
      take,
    };
  }, [apps]);

  const renderTableControl = () => {
    return (
      <form onSubmit={handleSubmit(submitSearch)}>
        <div className="row" style={{ justifyContent: 'space-between' }}>
          <div className="col-md-2">
            <input
              type="text"
              placeholder="Tìm Thiết Bị"
              className="form-control form-control-sm"
              style={{ height: '37px' }}
              name="Name"
              ref={register()}
            />
          </div>
          <div className="col-md-2">
            <input
              type="text"
              placeholder="Tìm Gói Thiết Bị"
              className="form-control form-control-sm"
              style={{ height: '37px' }}
              name="PackageName"
              ref={register()}
            />
          </div>
          <div className="col-md-4">
            <div className="btn-group">
              <button className="btn btn-info btn__config" type="submit">
                Tìm Kiếm
              </button>
            </div>
            <div className="btn-group ml-2">
              <button
                className="btn btn-info btn__config"
                type="reset"
                onClick={() => {
                  dispatch(MdmAppSlice.actions.getMdmAppAll(defaultPagi));
                  setPagi(defaultPagi);
                }}
              >
                Reset Tìm Kiếm
              </button>
            </div>
          </div>
          <div className="col-md-4 t text-right">
            <div className="btn-group">
              <button
                className="btn btn-primary btn__config"
                type="button"
                onClick={() => {
                  dispatchModal({
                    type: ModalType.OPEN,
                    payload: true,
                  });
                }}
              >
                Thêm mới
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (e, page) => {
    let changePage = Object.assign({}, pagi, { skip: page * pagi.take });
    const request = {
      ...changePage,
      ...getFormValues(),
    };
    setPagi(changePage);
    dispatch(MdmAppSlice.actions.getMdmAppAll(request));
  };

  const handleChangeRowsPerPage = e => {
    let changeRowPerPage = Object.assign({}, pagi, {
      take: +e.target.value,
      skip: 0,
    });
    const request = {
      ...changeRowPerPage,
      ...getFormValues(),
    };
    setPagi(changeRowPerPage);
    dispatch(MdmAppSlice.actions.getMdmAppAll(request));
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tableMdmApp}
      />
    );
  };

  const renderTable = () => {
    if (_.isEmpty(renderData) || !renderData.data.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tableMdmApp.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      renderData.data,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((row, idx) => {
      const device = _.cloneDeep(row);
      return (
        <TableRow key={row.id}>
          {tableMdmApp.map(column => {
            const value = device[column.id];
            if (column.id === 'no') {
              const { page, rowsPerPage } = renderData;
              return (
                <TableCell key={column.id}>
                  {page * rowsPerPage + idx + 1}
                </TableCell>
              );
            }
            if (column.id === 'updated_date') {
              return (
                <TableCell key={column.id}>
                  {dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE_TIME)}
                </TableCell>
              );
            }
            if (column.id === 'is_active') {
              return (
                <TableCell key={column.id}>
                  <Tooltip title="" enterDelay={500} leaveDelay={200}>
                    <i
                      style={{
                        color: value ? '#4caf50' : '#ff0000',
                        fontSize: '16px',
                      }}
                      className={`fa fa-${value ? 'check' : 'times'}-circle`}
                      aria-hidden="true"
                    ></i>
                  </Tooltip>
                </TableCell>
              );
            }
            if (column.id === 'actions') {
              return (
                <TableCell key={column.id}>
                  <Tooltip title={'Sửa'} enterDelay={500} leaveDelay={100}>
                    <button
                      className="btn btn-primary btn-xs"
                      onClick={() => {
                        dispatch(MdmAppSlice.actions.getMdmAppById(row.id));
                      }}
                    >
                      <i className="fa fa-pencil"></i>
                    </button>
                  </Tooltip>
                  <Tooltip title={'Xoá'} enterDelay={500} leaveDelay={100}>
                    <button
                      className="btn btn-danger btn-xs mx-2"
                      onClick={() => {
                        setAppId(row.id);
                        dispatchModal({
                          type: ModalType.DELETE,
                          payload: true,
                        });
                      }}
                    >
                      <i className="fa fa-trash-o"></i>
                    </button>
                  </Tooltip>
                </TableCell>
              );
            }
            return <TableCell key={column.id}>{value ? value : '-'}</TableCell>;
          })}
        </TableRow>
      );
    });
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={renderData.total}
          rowsPerPage={renderData.take}
          page={renderData.skip / renderData.take}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading ? <AppLoading loading={true} /> : null}
      <div className="col-md-12">
        {openModal && (
          <MDMModalApp
            open={openModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.OPEN,
                payload: false,
              });
            }}
          />
        )}
        <ModalConfirm
          title={'Bạn có chắc chắn muốn xoá thiết bị này?'}
          other={{
            bodyText: '',
          }}
          keyConfirm={''}
          open={deleteModal}
          onClose={() => {
            dispatchModal({
              type: ModalType.DELETE,
              payload: false,
            });
          }}
          confirmMethod={() => handleDeleteDevice()}
        />
        <div className="card card-topline-red">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
