import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { DeviceHttp } from '../services/mdmDevice.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';

const deviceRequest = new DeviceHttp();

export function* getMdmDeviceAll(api, action) {
  const response = yield call(api.getMdmDeviceAll, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmDeviceAllSuccess(data));
  } catch (error) {
    yield put(actions.getMdmDeviceAllFailure(_.get(error, 'message')));
  }
}

export function* getMdmDeviceById(api, action) {
  const response = yield call(api.getMdmDeviceById, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmDeviceByIdSuccess(data));
  } catch (error) {
    yield put(actions.getMdmDeviceByIdFailure(_.get(error, 'message')));
  }
}

export function* createMdmDevice(api, action) {
  const response = yield call(api.createMdmDevice, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.createMdmDeviceSuccess(data));
  } catch (error) {
    yield put(actions.createMdmDeviceFailure(_.get(error, 'message')));
  }
}

export function* updateMdmDevice(api, action) {
  const response = yield call(api.updateMdmDevice, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.updateMdmDeviceSuccess(data));
  } catch (error) {
    yield put(actions.updateMdmDeviceFailure(_.get(error, 'message')));
  }
}

export function* deleteMdmDevice(api, action) {
  const response = yield call(api.deleteMdmDevice, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.deleteMdmDeviceSuccess(data));
  } catch (error) {
    yield put(actions.deleteMdmDeviceFailure(_.get(error, 'message')));
  }
}

export function* MdmDeviceSaga() {
  yield all([
    yield takeLatest(
      actions.getMdmDeviceAll.type,
      getMdmDeviceAll,
      deviceRequest,
    ),
    yield takeLatest(
      actions.getMdmDeviceById.type,
      getMdmDeviceById,
      deviceRequest,
    ),
    yield takeLatest(
      actions.createMdmDevice.type,
      createMdmDevice,
      deviceRequest,
    ),
    yield takeLatest(
      actions.deleteMdmDevice.type,
      deleteMdmDevice,
      deviceRequest,
    ),
    yield takeLatest(
      actions.updateMdmDevice.type,
      updateMdmDevice,
      deviceRequest,
    ),
  ]);
}
