/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from '../../../store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_MDM_CONFIG_CONSTANT,
  RESPONSE_MDM_CONFIG_MESSAGE,
} from '../constants/mdmConfig.constant';

export interface MdmConfigState {
  loading?: boolean;
  success?: any;
  error?: any;
  mdmConfigs?: any;
  mdmConfig: any;
  mdmDeviceConfigs: any;
  mdmDeviceByid: any;
  mdmAppConfigs: any;
  mdmAppByid: any;
  mdmResetLogConfig: any;
  dataResetLogConfig: any;
  dataSendLogConfig: any;
  getLogConfig: any;
}

export const initialState: MdmConfigState = {
  loading: false,
  success: null,
  error: null,
  mdmConfigs: {},
  mdmConfig: {},
  mdmDeviceConfigs: {},
  mdmDeviceByid: {},
  mdmAppConfigs: {},
  mdmAppByid: {},
  mdmResetLogConfig: {},
  dataResetLogConfig: {},
  dataSendLogConfig: {},
  getLogConfig: undefined,
};

const mdmConfigFormSlice = createSlice({
  name: 'mdmConfig',
  initialState,
  reducers: {
    getMdmConfigAll(state, action) {
      state.loading = true;
    },

    getMdmConfigAllSuccess(state, action) {
      state.mdmConfigs = action.payload;
      state.loading = false;
    },

    getMdmConfigAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmConfigById(state, action) {
      state.loading = true;
    },

    getMdmConfigByIdSuccess(state, action) {
      state.mdmConfig = action.payload;
      state.loading = false;
    },

    getMdmConfigByIdFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmDeviceConfigAll(state, action) {
      state.loading = true;
    },

    getMdmDeviceConfigAllSuccess(state, action) {
      state.mdmDeviceConfigs = action.payload;
      state.loading = false;
    },

    getMdmDeviceConfigAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmDeviceByid(state, action) {
      state.loading = true;
    },

    getMdmDeviceByidSuccess(state, action) {
      state.mdmDeviceByid = action.payload;
      state.loading = false;
    },

    getMdmDeviceByidFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmAppConfigAll(state, action) {
      state.loading = true;
    },

    getMdmAppConfigAllSuccess(state, action) {
      state.mdmAppConfigs = action.payload;
      state.loading = false;
    },

    getMdmAppConfigAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmAppByid(state, action) {
      state.loading = true;
    },

    getMdmAppByidSuccess(state, action) {
      state.mdmAppByid = action.payload;
      state.loading = false;
    },

    getMdmAppByidFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    changeActiveAreaMdmConfig(state, action) {
      state.loading = true;
    },

    changeActiveAreaMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.CHANGE_ACTIVE_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.CHANGE_ACTIVE_MDM_CONFIG_SUCCESS,
      };
    },

    changeActiveAreaMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.CHANGE_ACTIVE_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.CHANGE_ACTIVE_MDM_CONFIG_FAIL,
      };
    },

    createMdmConfig(state, action) {
      state.loading = true;
    },

    createMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.CREATE_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.CREATE_MDM_CONFIG_SUCCESS,
      };
    },

    createMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.CREATE_MDM_CONFIG_FAIL,
        message: action.payload,
      };
    },

    updateMdmConfig(state, action) {
      state.loading = true;
    },

    updateMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.UPDATE_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.UPDATE_MDM_CONFIG_SUCCESS,
      };
    },

    updateMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.UPDATE_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.UPDATE_MDM_CONFIG_FAIL,
      };
    },

    deleteMdmConfig(state, action) {
      state.loading = true;
    },

    deleteMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.DELETE_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.DELETE_MDM_CONFIG_SUCCESS,
      };
    },

    deleteMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.DELETE_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.DELETE_MDM_CONFIG_FAIL,
      };
    },

    changeActiveMdmConfig(state, action) {
      state.loading = true;
    },

    changeActiveMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.CHANGE_ACTIVE_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.CHANGE_ACTIVE_MDM_CONFIG_SUCCESS,
      };
    },

    changeActiveMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.CHANGE_ACTIVE_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.CHANGE_ACTIVE_MDM_CONFIG_FAIL,
      };
    },

    dataRestLogConfig(state, action) {
      state.dataResetLogConfig = action.payload;
    },

    dataSendLogConfig(state, action) {
      state.dataSendLogConfig = action.payload;
    },

    resetLogMdmConfig(state, action) {
      state.loading = true;
    },

    resetLogMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.RESET_LOG_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.RESET_LOG_MDM_CONFIG_SUCCESS,
      };
    },

    resetLogMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.RESET_LOG_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.RESET_LOG_MDM_CONFIG_FAIL,
      };
    },

    sendLogMdmConfig(state, action) {
      state.loading = true;
    },

    sendLogMdmConfigSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.SEND_LOG_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.SEND_LOG_MDM_CONFIG_SUCCESS,
      };
    },

    sendLogMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.SEND_LOG_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.SEND_LOG_MDM_CONFIG_FAIL,
      };
    },

    getLogMdmConfig(state, action) {
      state.loading = true;
    },

    getLogMdmConfigSuccess(state, action) {
      state.getLogConfig = action.payload;
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.GET_LOG_MDM_CONFIG_SUCCESS,
        message: RESPONSE_MDM_CONFIG_MESSAGE.GET_LOG_MDM_CONFIG_SUCCESS,
      };
    },

    getLogMdmConfigFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONFIG_CONSTANT.GET_LOG_MDM_CONFIG_FAIL,
        message: RESPONSE_MDM_CONFIG_MESSAGE.GET_LOG_MDM_CONFIG_FAIL,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearError(state) {
      state.error = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearMdmConfig(state) {
      state.mdmConfig = {};
    },

    resetAll(state) {
      state.loading = false;
      state.success = null;
      state.error = null;
      state.mdmConfigs = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = mdmConfigFormSlice;
