import React from 'react';
import * as _ from 'lodash';
import * as MdmConfigSlice from 'store/mdmConfig/shared/slice';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch } from 'react-redux';
import { MdmConfigSaga } from 'store/mdmConfig/shared/saga';
import { useForm } from 'react-hook-form';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

export const areas = [
  { code: 'B', label: 'Khu B' },
  { code: 'D', label: 'Khu D' },
];

export const status = [
  { code: true, label: 'Bật' },
  { code: false, label: 'Tắt' },
];

export function MDMChangeActiveAreaModal({ open, onClose }) {
  useInjectReducer({
    key: MdmConfigSlice.sliceKey,
    reducer: MdmConfigSlice.reducer,
  });
  useInjectSaga({ key: MdmConfigSlice.sliceKey, saga: MdmConfigSaga });
  const { register, handleSubmit, errors } = useForm();
  const dispatch = useDispatch();

  const onSubmit = device => {
    if (!_.isEmpty(device)) {
      dispatch(MdmConfigSlice.actions.changeActiveAreaMdmConfig(device));
      onClose();
    }
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3">Tắt/Mở Ứng Dụng Theo Khu Vực</h4>
          <div className="form-group">
            <select
              className="form-control area_code"
              id="area_code"
              name="area_code"
              ref={register({ required: true })}
            >
              <option value="">Chọn Khu Vực</option>
              {areas.map(({ code, label }: any) => (
                <option key={code} value={code}>
                  {label}
                </option>
              ))}
            </select>
            {errors.area_code && errors.area_code?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng Chọn Khu Vực</span>
            ) : null}
          </div>
          <div className="form-group">
            <select
              className="form-control is_active"
              id="is_active"
              name="is_active"
              ref={register({ required: true })}
            >
              <option value="">Trạng Thái</option>
              {status.map(({ code, label }: any) => (
                <option key={code} value={code}>
                  {label}
                </option>
              ))}
            </select>
            {errors.is_active && errors.is_active?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng Chọn Trạng Thái
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="message">Tên Pagkage</label>
            <textarea
              id="message"
              className="form-control message"
              name="message"
              ref={register({ required: true })}
              placeholder="Thông Báo"
              autoComplete="off"
            ></textarea>
            {errors.message && errors.message?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Tin Nhắn</span>
            ) : null}
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Đóng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lưu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
