/* eslint-disable array-callback-return */
/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as MdmVersionSelector from 'store/mdmVersion/shared/selectors';
import * as MdmVersionSlice from 'store/mdmVersion/shared/slice';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { MdmVersionSaga } from 'store/mdmVersion/shared/saga';
import { useForm } from 'react-hook-form';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

export function MDMVersionModal({ open, onClose }) {
  useInjectReducer({
    key: MdmVersionSlice.sliceKey,
    reducer: MdmVersionSlice.reducer,
  });
  useInjectSaga({ key: MdmVersionSlice.sliceKey, saga: MdmVersionSaga });
  const initApp = useSelector(MdmVersionSelector.selecMdmAppVersions);
  const [name, setName] = useState<any>(null);
  const { register, handleSubmit, errors } = useForm();
  const dispatch = useDispatch();

  const onSubmit = version => {
    const uploadVersion = { ...version, BundleFile: version.BundleFile[0] };
    if (!_.isEmpty(uploadVersion)) {
      let formData = new FormData();
      for (let key in uploadVersion) {
        if (key === 'BundleFile') {
          formData.append(`BundleInfomation.${key}`, uploadVersion[key]);
        }
        if (key === 'ReleaseNote') {
          formData.append(`BundleInfomation.${key}`, uploadVersion[key]);
        }
        if (key !== 'BundleFile' && key !== 'ReleaseNote') {
          formData.append(key, uploadVersion[key]);
        }
      }
      dispatch(MdmVersionSlice.actions.createMdmVersion(formData));
      onClose();
    }
  };

  useEffect(() => {
    dispatch(MdmVersionSlice.actions.getMdmAppVersionAll(name));
  }, []);

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3">Tạo Mới Phiên Bản</h4>
          <div className="form-group">
            <label htmlFor="Name">Tên Phiên Bản</label>
            <input
              id="Name"
              type="text"
              className="form-control Name"
              name="Name"
              ref={register({ required: true })}
              placeholder="Tên Phiên Bản"
              autoComplete="off"
            />
            {errors.Name && errors.Name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng nhập Tên Phiên Bản
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <select
              className="form-control"
              name="ApplicationId"
              ref={register({ required: true })}
              onChange={e => {
                dispatch(
                  MdmVersionSlice.actions.getMdmAppVersionAll({
                    name: e.target.value,
                  }),
                );
                setName(e.target.value);
              }}
            >
              <option value="">Chọn Ứng Dụng</option>
              {!_.isEmpty(initApp) && initApp.data.length
                ? initApp.data.map((row, idx) => {
                    if (_.isEmpty(name)) {
                      return (
                        <option key={row.id} value={row.name}>
                          {row.name}
                        </option>
                      );
                    }
                    if (!_.isEmpty(name)) {
                      return (
                        <option key={row.id} value={row.id}>
                          {row.name}
                        </option>
                      );
                    }
                  })
                : null}
            </select>
            {errors.ApplicationId &&
            errors.ApplicationId?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng Chọn Úng Dụng</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="Code">Mã Phiên Bản</label>
            <input
              type="text"
              className="form-control Code"
              id="Code"
              name="Code"
              ref={register({ required: true })}
              placeholder="Mã Phiên Bản"
              autoComplete="off"
            />

            {errors.Code && errors.Code?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng nhập Mã Phiên Bản
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <input
              className="form-control BundleFile"
              type="file"
              id="BundleFile"
              name="BundleFile"
              ref={register({ required: true })}
            />
            {errors.BundleFile && errors.BundleFile?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng Nhập File</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="ReleaseNote">APK File Note</label>
            <input
              id="ReleaseNote"
              type="text"
              className="form-control ReleaseNote"
              name="ReleaseNote"
              ref={register({ required: true })}
              placeholder="APK File Note"
              autoComplete="off"
            />
            {errors.ReleaseNote && errors.ReleaseNote?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Note</span>
            ) : null}
          </div>
          <div className="form-check">
            <input
              className="form-check-input IsActive"
              type="checkbox"
              name="IsActive"
              id="IsActive"
              ref={register({ required: true })}
            />
            <label className="form-check-label" htmlFor="IsActive">
              Hoạt dộng
            </label>
            {errors.IsActive && errors.IsActive?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng Chọn Hoạt Đông</span>
            ) : null}
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Đóng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lưu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
