/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from '../../core/types';
import { initialState } from './slice';

const selectSettingState = (state: RootState) =>
  state.mdmConfig || initialState;

export const selectError = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.error,
);

export const selectSuccess = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.success,
);

export const selectLoading = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.loading,
);

export const selecMdmtConfigs = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.mdmConfigs,
);

export const selecMdmtDeviceConfigs = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.mdmDeviceConfigs,
);

export const selecMdmtDeviceByid = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.mdmDeviceByid,
);

export const selecMdmAppConfigs = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.mdmAppConfigs,
);

export const selecMdmAppByid = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.mdmAppByid,
);

export const selecMdmtConfig = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.mdmConfig,
);

export const selecMdmtDataResetConfig = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.dataResetLogConfig,
);

export const selecMdmDataSendConfig = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.dataSendLogConfig,
);

export const selecMdmGetLogConfig = createSelector(
  [selectSettingState],
  mdmConfigState => mdmConfigState.getLogConfig,
);
