import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { VersionHttp } from '../services/mdmVersion.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';

const versionRequest = new VersionHttp();

export function* getMdmVersionAll(api, action) {
  const response = yield call(api.getMdmVersionAll, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmVersionAllSuccess(data));
  } catch (error) {
    yield put(actions.getMdmVersionAllFailure(_.get(error, 'message')));
  }
}

export function* getMdmVersionById(api, action) {
  const response = yield call(api.getMdmVersionById, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmVersionByIdSuccess(data));
  } catch (error) {
    yield put(actions.getMdmVersionByIdFailure(_.get(error, 'message')));
  }
}

export function* getMdmAppVersionAll(api, action) {
  const response = yield call(api.getMdmAppVersionAll, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmAppVersionAllSuccess(data));
  } catch (error) {
    yield put(actions.getMdmAppVersionAllFailure(_.get(error, 'message')));
  }
}

export function* createMdmVersion(api, action) {
  const response = yield call(api.createMdmVersion, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.createMdmVersionSuccess(data));
  } catch (error) {
    yield put(actions.createMdmVersionFailure(_.get(error, 'message')));
  }
}

export function* updateMdmVersion(api, action) {
  const response = yield call(api.updateMdmVersion, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.updateMdmVersionSuccess(data));
  } catch (error) {
    yield put(actions.updateMdmVersionFailure(_.get(error, 'message')));
  }
}

export function* deleteMdmVersion(api, action) {
  const response = yield call(api.deleteMdmVersion, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.deleteMdmVersionSuccess(data));
  } catch (error) {
    yield put(actions.deleteMdmVersionFailure(_.get(error, 'message')));
  }
}

export function* MdmVersionSaga() {
  yield all([
    yield takeLatest(
      actions.getMdmVersionAll.type,
      getMdmVersionAll,
      versionRequest,
    ),
    yield takeLatest(
      actions.getMdmVersionById.type,
      getMdmVersionById,
      versionRequest,
    ),
    yield takeLatest(
      actions.getMdmAppVersionAll.type,
      getMdmAppVersionAll,
      versionRequest,
    ),
    yield takeLatest(
      actions.createMdmVersion.type,
      createMdmVersion,
      versionRequest,
    ),
    yield takeLatest(
      actions.deleteMdmVersion.type,
      deleteMdmVersion,
      versionRequest,
    ),
    yield takeLatest(
      actions.updateMdmVersion.type,
      updateMdmVersion,
      versionRequest,
    ),
  ]);
}
