/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect } from 'react';
import * as _ from 'lodash';
import * as MdmDeviceSlice from 'store/mdmDevice/shared/slice';
import * as MdmDeviceSelectors from 'store/mdmDevice/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { MdmDeviceSaga } from 'store/mdmDevice/shared/saga';
import { useForm } from 'react-hook-form';
import { areas, deviceTypesList } from '../pages/MDMDevice';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

export function MDMModalDevice({ open, onClose }) {
  useInjectReducer({
    key: MdmDeviceSlice.sliceKey,
    reducer: MdmDeviceSlice.reducer,
  });
  useInjectSaga({ key: MdmDeviceSlice.sliceKey, saga: MdmDeviceSaga });
  const initDevice = useSelector(MdmDeviceSelectors.selectMdmDevice);
  const { register, handleSubmit, errors, setValue } = useForm();
  const dispatch = useDispatch();

  const onSubmit = device => {
    if (_.isEmpty(initDevice)) {
      dispatch(MdmDeviceSlice.actions.createMdmDevice(device));
      onClose();
    }
    if (!_.isEmpty(initDevice)) {
      dispatch(
        MdmDeviceSlice.actions.updateMdmDevice({
          ...device,
          id: initDevice.id,
        }),
      );
      onClose();
    }
  };

  useEffect(() => {
    function initFormData(device) {
      if (_.isEmpty(device)) return;
      for (let key in device) {
        setValue(key, device[key]);
      }
    }
    setTimeout(() => initFormData(initDevice));
    return () => {
      dispatch(MdmDeviceSlice.actions.clearMdmDevice());
    };
  }, [initDevice]);

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3">
            {_.isEmpty(initDevice) ? 'Thêm Mới Thiết Bị' : 'Cập Nhật Thiết Bị'}
          </h4>
          <div className="form-group">
            <label htmlFor="name">Tên Thiết Bị</label>
            <input
              id="name"
              type="text"
              className="form-control name"
              name="name"
              ref={register({ required: true })}
              placeholder="Nhập Tên"
              autoComplete="off"
            />
            {errors.name && errors.name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Tên</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="code">Mã</label>
            <input
              id="code"
              type="text"
              className="form-control Code"
              name="code"
              ref={register({ required: true })}
              placeholder="Mã"
              autoComplete="off"
            />
            {errors.code && errors.code?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập mã</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="ip">IP</label>
            <input
              id="ip"
              type="text"
              className="form-control"
              name="ip"
              ref={register({ required: true })}
              placeholder="Nhập IP"
              autoComplete="off"
            />
            {errors.ip && errors.ip?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng nhập Ip thiết bị
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="area_code">Khu Vực</label>
            <select
              name="area_code"
              className="form-control"
              ref={register({ required: true })}
            >
              <option value="">Chọn Khu Vực</option>
              {areas.map(({ code, label }: any) => (
                <option key={code} value={code}>
                  {label}
                </option>
              ))}
            </select>
            {errors.type && errors.type?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Khu Vực</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="type">Loại Thiết Bị</label>
            <select
              name="type"
              className="form-control"
              ref={register({ required: true })}
            >
              <option value="">Loại Thiết Bị</option>
              {deviceTypesList.map(({ code, label }: any) => (
                <option key={code} value={code}>
                  {label}
                </option>
              ))}
            </select>
            {errors.type && errors.type?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng nhập loại thiết bị
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <input
              className="form-check-input ml-1 mr-2"
              type="checkbox"
              ref={register()}
              name="is_active"
            />
            <label
              className="form-check-label ml-2"
              htmlFor="is-actions"
              style={{ paddingLeft: '20px' }}
            >
              Hoạt dộng
            </label>
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Đóng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lưu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
