import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from '../../common/constants/common.constant';
import { MdmDeviceApiRoutes } from '../constants/mdmDevice.constant';

export interface FilterDevice {
  page: number;
  rowsPerPage: number;
}

export interface MdmDeviceModel {
  id?: string;
  name: string;
  code: string;
  type: string;
  ip: string;
  is_active: boolean;
  updated_date: string;
  area_code: string;
}

export class DeviceHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEVICE) {
    this.request = new HttpRequest(endPoint).request;
  }

  private configRequestBody = (mdmDevice: MdmDeviceModel) => {
    let body = {
      name: mdmDevice.name,
      code: mdmDevice.code,
      type: mdmDevice.type,
      ip: mdmDevice.ip,
      is_active: mdmDevice.is_active,
      area_code: mdmDevice.area_code,
      application_ids: [],
    };
    if (mdmDevice.id) {
      return {
        ...body,
        id: mdmDevice.id,
        device_command: 0,
      };
    }
    return body;
  };

  public getMdmDeviceAll = (filter: any): Promise<any> => {
    return this.request.get(MdmDeviceApiRoutes.GET_ALL_MDM_DEVICE, filter);
  };

  public getMdmDeviceById = (id: string): Promise<any> => {
    return this.request.get(MdmDeviceApiRoutes.GET_BY_ID_MDM_DEVICE + '/' + id);
  };

  public createMdmDevice = (mdmDevice: MdmDeviceModel): Promise<any> => {
    return this.request.post(MdmDeviceApiRoutes.CREATE_MDM_DEVICE, {
      ...this.configRequestBody(mdmDevice),
    });
  };

  public updateMdmDevice = (mdmDevice: any): Promise<any> => {
    return this.request.post(MdmDeviceApiRoutes.UPDATE_MDM_DEVICE, {
      ...this.configRequestBody(mdmDevice),
    });
  };

  public deleteMdmDevice = (id: string): Promise<any> => {
    return this.request.post(MdmDeviceApiRoutes.DELETE_MDM_DEVICE + '/' + id);
  };
}
