/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState, useReducer, useMemo, useRef } from 'react';
import * as MdmConfigSelector from 'store/mdmConfig/shared/selectors';
import * as MdmConfigSlice from 'store/mdmConfig/shared/slice';
import * as _ from 'lodash';
import { useDispatch, useSelector } from 'react-redux';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { MdmConfigSaga } from 'store/mdmConfig/shared/saga';
import { makeStyles } from '@material-ui/core/styles';
import { Switch, Tooltip } from '@material-ui/core';
import { AppHelper, Order } from 'utils/app.helper';
import {
  DEFAULT_FORMAT_DATE_TIME,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useForm } from 'react-hook-form';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import TableHeaderSort from 'app/components/TableHeaderSort';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';
import { MDMChangeActiveAreaModal } from '../components/MDMChangeActiveAreaModal';
import { MDMConfigModal } from '../components/MDMConfigModal';
import { ModalConfirm } from 'app/components/Modal/Confirm';
import { MDMResetLogModal } from '../components/MDMResetLogModal';
import { MDMGetLogModal } from '../components/MDMGetLogModal';
import { RESPONSE_MDM_CONFIG_CONSTANT } from 'store/mdmConfig/constants/mdmConfig.constant';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  tableMdmConfig: {
    width: '100%',
    minWidth: '1600px',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export const tableMdmConfig: any[] = [
  { id: 'application_name', label: 'Tên Ứng Dụng', minWidth: 120 },
  { id: 'device_name', label: 'Tên Thiết Bị', minWidth: 120 },
  { id: 'area_code', label: 'Khu Vực', minWidth: 120 },
  { id: 'version_name', label: 'Tên Phiên Bản', minWidth: 120 },
  { id: 'is_active', label: 'Bật/Tắt', minWidth: 170 },
  { id: 'updated_date', label: 'Cập Nhật', minWidth: 170 },
  { id: 'version_code', label: 'Hoạt Động', minWidth: 120 },
  { id: 'actions', label: 'Chức năng', minWidth: 170 },
];

export const areas = [
  { code: 'B', label: 'Khu B' },
  { code: 'D', label: 'Khu D' },
];

export const status = [
  { code: true, label: 'Bật' },
  { code: false, label: 'Tắt' },
];

interface ModalState {
  openModal: boolean;
  updateModal: boolean;
  deleteModal: boolean;
  logModal: boolean;
  changeActiveModal: boolean;
  getLogModal: boolean;
}

enum ModalType {
  OPEN = 'OPEN',
  UPDATE = 'UPDATE',
  LOG_MODAL = 'LOG_MODAL',
  DELETE = 'DELETE',
  CLOSE_ALL = 'CLOSE_ALL',
  CHANGE_ACTIVE_AREA = 'CHANGE_ACTIVE_AREA',
  GET_LOG = 'GET_LOG',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  openModal: false,
  updateModal: false,
  deleteModal: false,
  logModal: false,
  changeActiveModal: false,
  getLogModal: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN:
      return {
        ...state,
        openModal: payload,
      };
    case ModalType.UPDATE:
      return {
        ...state,
        updateModal: payload,
      };
    case ModalType.LOG_MODAL:
      return {
        ...state,
        logModal: payload,
      };
    case ModalType.CHANGE_ACTIVE_AREA:
      return {
        ...state,
        changeActiveModal: payload,
      };
    case ModalType.DELETE:
      return {
        ...state,
        deleteModal: payload,
      };
    case ModalType.GET_LOG:
      return {
        ...state,
        getLogModal: payload,
      };
    case ModalType.CLOSE_ALL:
      return initModalState;
    default:
      return state;
  }
};

const defaultPagi = {
  take: 10,
  skip: 0,
};

export function MDMConfig() {
  const classes = useStyles();
  const dispatch = useDispatch();
  useInjectReducer({
    key: MdmConfigSlice.sliceKey,
    reducer: MdmConfigSlice.reducer,
  });
  useInjectSaga({ key: MdmConfigSlice.sliceKey, saga: MdmConfigSaga });
  const { enqueueSnackbar } = useSnackbar();
  const dateUtil = new DateFnsUtils();
  const notifyController = new NotifyService(enqueueSnackbar);
  const error: any = useSelector<any>(MdmConfigSelector.selectError);
  const success: any = useSelector<any>(MdmConfigSelector.selectSuccess);
  const loading: any = useSelector<any>(MdmConfigSelector.selectLoading);
  const configs: any = useSelector<any>(MdmConfigSelector.selecMdmtConfigs);
  const [
    {
      openModal,
      updateModal,
      changeActiveModal,
      deleteModal,
      logModal,
      getLogModal,
    },
    dispatchModal,
  ] = useReducer(reduceModal, initModalState);
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const { handleSubmit, register, reset, getValues } = useForm();
  const [deviceId, setDeviceId] = React.useState<any>(null);
  const [appId, setAppId] = React.useState<any>(null);
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('name');
  const recallRef = useRef<any>(null);
  const refReCallCount = useRef<number>(1);

  useEffect(() => {
    dispatch(MdmConfigSlice.actions.getMdmConfigAll(defaultPagi));
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case MdmConfigSlice.actions.getMdmConfigAllSuccess.type:
          refReCallCount.current = 1;
          recallSearch();
          break;
        case MdmConfigSlice.actions.getMdmConfigAllFailure.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          recallError();
          break;
        case MdmConfigSlice.actions.createMdmConfigFailure.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          break;
        case MdmConfigSlice.actions.getMdmConfigByIdSuccess.type:
          dispatchModal({
            type: ModalType.UPDATE,
            payload: true,
          });
          break;
        case MdmConfigSlice.actions.dataRestLogConfig.type:
          dispatchModal({
            type: ModalType.LOG_MODAL,
            payload: true,
          });
          break;
        case MdmConfigSlice.actions.dataSendLogConfig.type:
          dispatchModal({
            type: ModalType.LOG_MODAL,
            payload: true,
          });
          break;
        case MdmConfigSlice.actions.getLogMdmConfigSuccess.type:
          dispatchModal({
            type: ModalType.GET_LOG,
            payload: true,
          });
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
      if (recallRef.current) {
        clearTimeout(recallRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      dispatch(MdmConfigSlice.actions.getMdmConfigAll(pagi));
      dispatch(MdmConfigSlice.actions.clearSuccess());
    }
  }, [success]);

  const researchConfig = () => {
    return {
      ...getFormValues(),
      ...pagi,
    };
  };

  const recallError = () => {
    if (refReCallCount.current >= 3) {
      return notifyController
        .setNotifyMessage(
          'Tải danh sách cài đặt thất bại, vui lòng tải lại trang ',
        )
        .setWaringNotify();
    }
    notifyController
      .setNotifyMessage(
        'Tải danh sách cài đặt thất bại, đang cố gắng tải lại lần ' +
          refReCallCount.current,
      )
      .setWaringNotify();
    dispatch(MdmConfigSlice.actions.getMdmConfigAll(researchConfig()));
    refReCallCount.current = refReCallCount.current + 1;
  };

  const recallSearch = () => {
    if (recallRef.current) {
      clearTimeout(recallRef.current);
    }
    recallRef.current = setTimeout(() => {
      setPagi(defaultPagi);
      dispatch(MdmConfigSlice.actions.getMdmConfigAll(researchConfig()));
    }, 50000);
  };

  const submitSearch = (values: any) => {
    setPagi(defaultPagi);
    dispatch(
      MdmConfigSlice.actions.getMdmConfigAll({
        ...values,
        pagi,
      }),
    );
  };

  const getFormValues = (): any => {
    return {
      DeviceName: getValues('DeviceName'),
      AreaCode: getValues('AreaCode'),
      ApplicationName: getValues('ApplicationName'),
      VersionName: getValues('VersionName'),
      IsActive: getValues('IsActive'),
    };
  };

  const handleDeleteConfig = () => {
    dispatch(MdmConfigSlice.actions.deleteMdmConfig({ deviceId, appId }));
  };

  const renderData: any = useMemo(() => {
    const { data = [], total } = configs;
    const { skip, take } = pagi;
    return {
      data,
      total,
      skip,
      take,
    };
  }, [configs]);

  const renderTableControl = () => {
    return (
      <form onSubmit={handleSubmit(submitSearch)} className="row">
        <div className="col-md-2 col-lg-2">
          <input
            type="text"
            placeholder="Tìm Theo Ứng Dụng"
            className="form-control"
            style={{ height: '37px' }}
            name="ApplicationName"
            ref={register()}
          />
        </div>
        <div className="col-md-2 col-lg-2">
          <input
            type="text"
            placeholder="Tìm Theo Thiết Bị"
            className="form-control"
            style={{ height: '37px' }}
            name="DeviceName"
            ref={register()}
          />
        </div>
        <div className="col-md-2 col-lg-2">
          <input
            type="text"
            placeholder="Tìm Theo Phiên Bản"
            className="form-control"
            style={{ height: '37px' }}
            name="VersionName"
            ref={register()}
          />
        </div>
        <div className="col-md-2 col-lg-2">
          <select className="form-control" name="AreaCode" ref={register()}>
            <option value="">Chọn Khu Vực</option>
            {areas.map(({ code, label }: any) => (
              <option key={code} value={code}>
                {label}
              </option>
            ))}
          </select>
        </div>
        <div className="col-md-2 col-lg-2">
          <select name="IsActive" className="form-control" ref={register()}>
            <option value="">Trạng Thái</option>
            {status.map(({ code, label }: any) => (
              <option key={code} value={code}>
                {label}
              </option>
            ))}
          </select>
        </div>
        <div className="col-md-2 col-lg-2">
          <div className="btn-group">
            <button
              className="btn btn-success btn__config"
              type="submit"
              style={{ width: '100px' }}
            >
              Tìm Kiếm
            </button>
          </div>
        </div>
        <div className="col-12 mt-3">
          <div className="btn-group mr-3">
            <button
              className="btn btn-warning btn__config"
              type="reset"
              onClick={() => {
                // setStInterVal(false);
                dispatch(MdmConfigSlice.actions.getMdmConfigAll(defaultPagi));
                setPagi(defaultPagi);
              }}
            >
              Reset Tìm Kiếm
            </button>
          </div>
          <div className="btn-group mr-3">
            <button
              className="btn btn-danger btn__config"
              type="button"
              onClick={() => {
                dispatchModal({
                  type: ModalType.CHANGE_ACTIVE_AREA,
                  payload: true,
                });
              }}
            >
              Tắt/Mở Theo Khu Vực
            </button>
          </div>
          <div className="btn-group">
            <button
              className="btn btn-primary btn__config"
              type="button"
              onClick={() => {
                dispatchModal({
                  type: ModalType.OPEN,
                  payload: true,
                });
              }}
            >
              Thêm Cài Đặt
            </button>
          </div>
        </div>
      </form>
    );
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (e, page) => {
    let changePage = Object.assign({}, pagi, { skip: page * pagi.take });
    const request = {
      ...changePage,
      ...getFormValues(),
    };
    setPagi(changePage);
    dispatch(MdmConfigSlice.actions.getMdmConfigAll(request));
  };

  const handleChangeRowsPerPage = e => {
    let changeRowPerPage = Object.assign({}, pagi, {
      take: +e.target.value,
      skip: 0,
    });
    const request = {
      ...changeRowPerPage,
      ...getFormValues(),
    };
    setPagi(changeRowPerPage);
    dispatch(MdmConfigSlice.actions.getMdmConfigAll(request));
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tableMdmConfig}
      />
    );
  };

  const renderTable = () => {
    if (_.isEmpty(renderData) || !renderData.data.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tableMdmConfig.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      renderData.data,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((row, idx) => {
      const device = _.cloneDeep(row);
      return (
        <TableRow key={idx}>
          {tableMdmConfig.map(column => {
            const value = device[column.id];
            if (column.id === 'no') {
              const { page, rowsPerPage } = renderData;
              return (
                <TableCell key={column.id}>
                  {page * rowsPerPage + idx + 1}
                </TableCell>
              );
            }
            if (column.id === 'area_code') {
              const areasType: any = areas.find(type => type.code === value);
              return (
                <TableCell key={column.id}>
                  {areasType ? areasType?.label : value}
                </TableCell>
              );
            }
            if (column.id === 'is_active') {
              return (
                <TableCell key={column.id}>
                  <Tooltip title="" enterDelay={500} leaveDelay={200}>
                    <i
                      style={{
                        color: value ? '#4caf50' : '#ff0000',
                        fontSize: '16px',
                      }}
                      className={`fa fa-${value ? 'check' : 'times'}-circle`}
                      aria-hidden="true"
                    ></i>
                  </Tooltip>
                </TableCell>
              );
            }
            if (column.id === 'updated_date') {
              return (
                <TableCell key={column.id}>
                  {dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE_TIME)}
                </TableCell>
              );
            }
            if (column.id === 'version_code') {
              return (
                <TableCell key={column.id}>
                  <Tooltip title="" enterDelay={500} leaveDelay={200}>
                    <i
                      style={{
                        color: value === 2 ? '#4caf50' : '#ff0000',
                        fontSize: '16px',
                      }}
                      className={`fa fa-${
                        value === 2 ? 'check' : 'times'
                      }-circle`}
                      aria-hidden="true"
                    ></i>
                  </Tooltip>
                </TableCell>
              );
            }
            if (column.id === 'actions') {
              return (
                <TableCell key={column.id}>
                  <Tooltip title={'Sửa'} enterDelay={500} leaveDelay={100}>
                    <button
                      className="btn btn-info btn-xs"
                      onClick={() => {
                        dispatch(
                          MdmConfigSlice.actions.getMdmDeviceConfigAll({
                            DeviceName: row.device_name,
                          }),
                        );
                        dispatch(
                          MdmConfigSlice.actions.getMdmAppConfigAll({
                            Name: row.application_name,
                          }),
                        );
                        dispatch(
                          MdmConfigSlice.actions.getMdmConfigById({
                            deviceId: row.device_id,
                            appId: row.application_id,
                          }),
                        );
                      }}
                    >
                      <i className="fa fa-pencil"></i>
                    </button>
                  </Tooltip>
                  <Tooltip
                    title={'Restart App'}
                    enterDelay={500}
                    leaveDelay={100}
                  >
                    <button
                      className="btn btn-success btn-xs mx-2"
                      onClick={() => {
                        dispatch(
                          MdmConfigSlice.actions.dataSendLogConfig(undefined),
                        );
                        dispatch(MdmConfigSlice.actions.dataRestLogConfig(row));
                      }}
                    >
                      <i className="fa fa-refresh"></i>
                    </button>
                  </Tooltip>
                  <Tooltip title={'Xoá'} enterDelay={500} leaveDelay={100}>
                    <button
                      className="btn btn-danger btn-xs mx-2"
                      onClick={() => {
                        handelDeleteModal(row.device_id, row.application_id);
                      }}
                    >
                      <i className="fa fa-times"></i>
                    </button>
                  </Tooltip>
                  <Tooltip title={'Gửi Log'} enterDelay={500} leaveDelay={100}>
                    <button
                      className="btn btn-warning btn-xs mx-2"
                      onClick={() => {
                        dispatch(
                          MdmConfigSlice.actions.dataRestLogConfig(undefined),
                        );
                        dispatch(MdmConfigSlice.actions.dataSendLogConfig(row));
                      }}
                    >
                      <i className="fa fa-paper-plane"></i>
                    </button>
                  </Tooltip>
                  <Tooltip title={'Xem Log'} enterDelay={500} leaveDelay={100}>
                    <button
                      className="btn btn-info btn-xs mx-2"
                      onClick={() => {
                        dispatch(
                          MdmConfigSlice.actions.getLogMdmConfig({
                            device_id: row.device_id,
                            application_id: row.application_id,
                          }),
                        );
                      }}
                    >
                      <i className="fa fa-history"></i>
                    </button>
                  </Tooltip>
                  <Tooltip
                    title={'[Tắt / Mở]'}
                    enterDelay={500}
                    leaveDelay={100}
                  >
                    <Switch
                      checked={row.is_active}
                      color="secondary"
                      onClick={() => {
                        handleChangeActive(
                          row.device_id,
                          row.application_id,
                          row.is_active,
                          row.area_code,
                          row.message,
                        );
                      }}
                    />
                  </Tooltip>
                </TableCell>
              );
            }
            return <TableCell key={column.id}>{value ? value : '-'}</TableCell>;
          })}
        </TableRow>
      );
    });
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={renderData.total}
          rowsPerPage={renderData.take}
          page={renderData.skip / renderData.take}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  const handelDeleteModal = (deviceId, appId) => {
    setDeviceId(deviceId);
    setAppId(appId);
    dispatchModal({ type: ModalType.DELETE, payload: true });
  };

  const handleChangeActive = (
    device_id,
    application_id,
    is_active,
    area_code,
    message,
  ) => {
    const dataChangeActive = {
      device_id,
      application_id,
      is_active: !is_active,
      area_code,
      message,
    };
    dispatch(MdmConfigSlice.actions.changeActiveMdmConfig(dataChangeActive));
  };

  return (
    <div className="row">
      {loading ? <AppLoading loading={true} /> : null}
      <div className="col-md-12">
        {openModal && (
          <MDMConfigModal
            open={openModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.OPEN,
                payload: false,
              });
            }}
          />
        )}
        {updateModal && (
          <MDMConfigModal
            open={updateModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.UPDATE,
                payload: false,
              });
            }}
          />
        )}
        {logModal && (
          <MDMResetLogModal
            open={logModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.LOG_MODAL,
                payload: false,
              });
            }}
          />
        )}
        {getLogModal && (
          <MDMGetLogModal
            open={getLogModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.GET_LOG,
                payload: false,
              });
            }}
          />
        )}
        {changeActiveModal && (
          <MDMChangeActiveAreaModal
            open={changeActiveModal}
            onClose={() => {
              dispatchModal({
                type: ModalType.CHANGE_ACTIVE_AREA,
                payload: false,
              });
            }}
          />
        )}
        <ModalConfirm
          title={'Bạn có chắc chắn muốn xoá thiết bị này?'}
          other={{
            bodyText: '',
          }}
          keyConfirm={''}
          open={deleteModal}
          onClose={() =>
            dispatchModal({
              type: ModalType.DELETE,
              payload: false,
            })
          }
          confirmMethod={() => handleDeleteConfig()}
        />
        <div className="card card-topline-red">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table className={classes.tableMdmConfig}>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
