/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from '../../core/types';
import { initialState } from './slice';

const selectSettingState = (state: RootState) => state.mdmApp || initialState;

export const selectError = createSelector(
  [selectSettingState],
  mdmAppState => mdmAppState.error,
);

export const selectSuccess = createSelector(
  [selectSettingState],
  mdmAppState => mdmAppState.success,
);

export const selectLoading = createSelector(
  [selectSettingState],
  mdmAppState => mdmAppState.loading,
);

export const selecMdmtApps = createSelector(
  [selectSettingState],
  mdmAppState => mdmAppState.mdmApps,
);

export const selectMdmApp = createSelector(
  [selectSettingState],
  mdmAppState => mdmAppState.mdmApp,
);
