/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from '../../../store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_MDM_CONSTANT,
  RESPONSE_MDM_MESSAGE,
} from '../constants/mdmDevice.constant';

export interface MdmDeviceState {
  loading?: boolean;
  success?: any;
  error?: any;
  mdmDevices?: any;
  mdmDevice: any;
}

export const initialState: MdmDeviceState = {
  loading: false,
  success: null,
  error: null,
  mdmDevices: {},
  mdmDevice: {},
};

const mdmDeviceFormSlice = createSlice({
  name: 'mdmDevice',
  initialState,
  reducers: {
    getMdmDeviceAll(state, action) {
      state.loading = true;
    },

    getMdmDeviceAllSuccess(state, action) {
      state.mdmDevices = action.payload;
      state.loading = false;
    },

    getMdmDeviceAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmDeviceById(state, action) {
      state.loading = true;
    },

    getMdmDeviceByIdSuccess(state, action) {
      state.mdmDevice = action.payload;
      state.loading = false;
    },

    getMdmDeviceByIdFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    createMdmDevice(state, action) {
      state.loading = true;
    },

    createMdmDeviceSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONSTANT.CREATE_MDM_DEVICE_SUCCESS,
        message: RESPONSE_MDM_MESSAGE.CREATE_MDM_DEVICE_SUCCESS,
      };
    },

    createMdmDeviceFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONSTANT.CREATE_MDM_DEVICE_FAIL,
        message: action.payload,
      };
    },

    updateMdmDevice(state, action) {
      state.loading = true;
    },

    updateMdmDeviceSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONSTANT.UPDATE_MDM_DEVICE_SUCCESS,
        message: RESPONSE_MDM_MESSAGE.UPDATE_MDM_DEVICE_SUCCESS,
      };
    },

    updateMdmDeviceFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONSTANT.UPDATE_MDM_DEVICE_SUCCESS,
        message: RESPONSE_MDM_MESSAGE.UPDATE_MDM_DEVICE_SUCCESS,
      };
    },

    deleteMdmDevice(state, action) {
      state.loading = true;
    },

    deleteMdmDeviceSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONSTANT.DELETE_MDM_DEVICE_SUCCESS,
        message: RESPONSE_MDM_MESSAGE.DELETE_MDM_DEVICE_SUCCESS,
      };
    },

    deleteMdmDeviceFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_CONSTANT.DELETE_MDM_DEVICE_FAIL,
        message: RESPONSE_MDM_MESSAGE.DELETE_MDM_DEVICE_FAIL,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearError(state) {
      state.error = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearMdmDevice(state) {
      state.mdmDevice = {};
    },

    resetAll(state) {
      // state.cacheDeviceData = null;
      state.loading = false;
      state.success = null;
      state.error = null;
      state.mdmDevices = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = mdmDeviceFormSlice;
