/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from '../../../store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_MDM_VERSION_CONSTANT,
  RESPONSE_MDM_VERSION_MESSAGE,
} from '../constants/mdmVersion.constant';

export interface MdmVersionState {
  cacheDeviceData?: any;
  cacheSetting?: any;
  loading?: boolean;
  success?: any;
  error?: any;
  versions?: any;
  version: any;
  appVersions: any;
}

export const initialState: MdmVersionState = {
  cacheDeviceData: null,
  loading: false,
  success: null,
  error: null,
  versions: {},
  version: {},
  appVersions: [],
};

const mdmVersionFormSlice = createSlice({
  name: 'mdmVersion',
  initialState,
  reducers: {
    getMdmVersionAll(state, action) {
      state.loading = true;
    },

    getMdmVersionAllSuccess(state, action) {
      state.versions = action.payload;
      state.loading = false;
    },

    getMdmVersionAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmAppVersionAll(state, action) {
      state.loading = true;
    },

    getMdmAppVersionAllSuccess(state, action) {
      state.appVersions = action.payload;
      state.loading = false;
    },

    getMdmAppVersionAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmVersionById(state, action) {
      state.loading = true;
    },

    getMdmVersionByIdSuccess(state, action) {
      state.version = action.payload;
      state.loading = false;
    },

    getMdmVersionByIdFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    createMdmVersion(state, action) {
      state.loading = true;
    },

    createMdmVersionSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_VERSION_CONSTANT.CREATE_MDM_VERSION_SUCCESS,
        message: RESPONSE_MDM_VERSION_MESSAGE.CREATE_MDM_VERSION_SUCCESS,
      };
    },

    createMdmVersionFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_VERSION_CONSTANT.CREATE_MDM_VERSION_FAIL,
        message: action.payload,
      };
    },

    updateMdmVersion(state, action) {
      state.loading = true;
    },

    updateMdmVersionSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_VERSION_CONSTANT.UPDATE_MDM_VERSION_SUCCESS,
        message: RESPONSE_MDM_VERSION_MESSAGE.UPDATE_MDM_VERSION_SUCCESS,
      };
    },

    updateMdmVersionFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_VERSION_CONSTANT.UPDATE_MDM_VERSION_FAIL,
        message: RESPONSE_MDM_VERSION_MESSAGE.UPDATE_MDM_VERSION_FAIL,
      };
    },

    deleteMdmVersion(state, action) {
      state.loading = true;
    },

    deleteMdmVersionSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_VERSION_CONSTANT.DELETE_MDM_VERSION_SUCCESS,
        message: RESPONSE_MDM_VERSION_MESSAGE.DELETE_MDM_VERSION_SUCCESS,
      };
    },

    deleteMdmVersionFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_VERSION_CONSTANT.DELETE_MDM_VERSION_FAIL,
        message: RESPONSE_MDM_VERSION_MESSAGE.DELETE_MDM_VERSION_FAIL,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearError(state) {
      state.error = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearMdmVersion(state) {
      state.version = {};
    },

    resetAll(state) {
      state.cacheDeviceData = null;
      state.loading = false;
      state.success = null;
      state.error = null;
      state.versions = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = mdmVersionFormSlice;
