/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';
import * as _ from 'lodash';
import * as MdmConfigSlice from 'store/mdmConfig/shared/slice';
import * as MdmConfigSelector from 'store/mdmConfig/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { MdmConfigSaga } from 'store/mdmConfig/shared/saga';
import { useForm } from 'react-hook-form';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

export function MDMResetLogModal({ open, onClose }) {
  const dispatch = useDispatch();
  useInjectReducer({
    key: MdmConfigSlice.sliceKey,
    reducer: MdmConfigSlice.reducer,
  });
  useInjectSaga({ key: MdmConfigSlice.sliceKey, saga: MdmConfigSaga });
  const initDataReset = useSelector(MdmConfigSelector.selecMdmtDataResetConfig);
  const initDataSend = useSelector(MdmConfigSelector.selecMdmDataSendConfig);

  const { register, handleSubmit, errors, setValue } = useForm();

  useEffect(() => {
    function initFormData(dataReset) {
      if (_.isEmpty(dataReset)) return;
      for (let key in dataReset) {
        setValue(key, dataReset[key]);
      }
    }
    if (!_.isEmpty(initDataReset)) {
      setTimeout(() => initFormData(initDataReset));
    } else if (!_.isEmpty(initDataSend)) {
      setTimeout(() => initFormData(initDataSend));
    }
    return () => {
      dispatch(MdmConfigSlice.actions.clearMdmConfig());
    };
  }, [initDataReset]);

  const onSubmit = config => {
    if (!_.isEmpty(initDataReset)) {
      const { device_id, application_id, package_name } = initDataReset;
      let body = {
        device_id,
        application_id,
        package_name,
        command_description: config.command_description,
      };
      dispatch(MdmConfigSlice.actions.resetLogMdmConfig(body));
      onClose();
    } else if (!_.isEmpty(initDataSend)) {
      const { device_id, application_id, package_name } = initDataSend;
      let body = {
        device_id,
        application_id,
        package_name,
        command_description: config.command_description,
      };
      dispatch(MdmConfigSlice.actions.sendLogMdmConfig(body));
      onClose();
    }
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3">
            {initDataReset
              ? 'Tạo Lệnh Khởi Động Lại Ứng Dụng'
              : initDataSend
              ? 'Tạo Lệnh Gửi Log Của Ứng Dụng'
              : '-'}
          </h4>

          <div className="form-group">
            <label htmlFor="application_name">Tên Ứng Dụng</label>
            <input
              id="application_name"
              type="text"
              className="form-control application_name"
              name="application_name"
              ref={register({ required: true })}
              placeholder="Nhập Tên Ứng Dụng"
              autoComplete="off"
              disabled
            />
            {errors.application_name &&
            errors.application_name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Ứng Dụng</span>
            ) : null}
          </div>

          <div className="form-group">
            <label htmlFor="package_name">Tên Gói</label>
            <input
              id="package_name"
              type="text"
              className="form-control package_name"
              name="package_name"
              ref={register({ required: true })}
              placeholder="Nhập Tên Gói"
              autoComplete="off"
              disabled
            />
            {errors.name && errors.name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Tên Gói</span>
            ) : null}
          </div>

          <div className="form-group">
            <label htmlFor="device_name">Tên Thiết Bị</label>
            <input
              id="device_name"
              type="text"
              className="form-control device_name"
              name="device_name"
              ref={register({ required: true })}
              placeholder="Nhập Tên Thiết Bị"
              autoComplete="off"
              disabled
            />
            {errors.name && errors.name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng nhập Tên Thiết Bị
              </span>
            ) : null}
          </div>

          <div className="form-group">
            <label htmlFor="command_description">Thông Báo Lệnh</label>
            <textarea
              id="command_description"
              className="form-control command_description"
              name="command_description"
              ref={register({ required: true })}
              placeholder="Thông Báo Lệnh"
              autoComplete="off"
            ></textarea>
            {errors.command_description &&
            errors.command_description?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Lệnh</span>
            ) : null}
          </div>

          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Đóng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lưu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
