import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from '../../common/constants/common.constant';
import { MdmAppApiRoutes } from '../constants/mdmApp.constant';

export interface FilterApp {
  take: number;
  skip: number;
}

export interface MdmAppModel {
  id: string;
  name: string;
  package_name: string;
  is_active: boolean;
}

export class AppHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEVICE) {
    this.request = new HttpRequest(endPoint).request;
  }

  private configRequestBody = (mdmApp: MdmAppModel) => {
    let body = {
      id: mdmApp.id,
      name: mdmApp.name,
      package_name: mdmApp.package_name,
      is_active: mdmApp.is_active,
    };
    if (mdmApp.id) {
      return {
        ...body,
        id: mdmApp.id,
        version_ids: [],
      };
    }
    return body;
  };

  public getMdmAppAll = (filter: FilterApp): Promise<any> => {
    return this.request.get(MdmAppApiRoutes.GET_ALL_MDM_APP, filter);
  };

  public getMdmAppById = (id: string): Promise<any> => {
    return this.request.get(MdmAppApiRoutes.GET_BY_ID_MDM_APP + '/' + id);
  };

  public createMdmApp = (mdmApp: MdmAppModel): Promise<any> => {
    return this.request.post(MdmAppApiRoutes.CREATE_MDM_APP, {
      ...this.configRequestBody(mdmApp),
    });
  };

  public updateMdmApp = (mdmApp: any): Promise<any> => {
    return this.request.post(MdmAppApiRoutes.UPDATE_MDM_APP, {
      ...this.configRequestBody(mdmApp),
    });
  };

  public deleteMdmApp = (id: string): Promise<any> => {
    return this.request.post(MdmAppApiRoutes.DELETE_MDM_APP + '/' + id);
  };
}
