/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as MdmConfigSlice from 'store/mdmConfig/shared/slice';
import * as MdmConfigSelector from 'store/mdmConfig/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { MdmConfigSaga } from 'store/mdmConfig/shared/saga';
import { useForm } from 'react-hook-form';
import { AppHelper } from 'utils/app.helper';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';

const defaultDeviceName = {
  DeviceName: '',
};

const defaultName = {
  Name: '',
};

export const defaultLogTime = [
  { code: '00:00:00', label: '12:00 AM' },
  { code: '01:00:00', label: '1:00 AM' },
  { code: '02:00:00', label: '2:00 AM' },
  { code: '03:00:00', label: '3:00 AM' },
  { code: '04:00:00', label: '4:00 AM' },
  { code: '05:00:00', label: '5:00 AM' },
  { code: '06:00:00', label: '6:00 AM' },
  { code: '07:00:00', label: '7:00 AM' },
  { code: '08:00:00', label: '8:00 AM' },
  { code: '09:00:00', label: '9:00 AM' },
  { code: '10:00:00', label: '10:00 AM' },
  { code: '11:00:00', label: '11:00 AM' },
  { code: '12:00:00', label: '12:00 PM' },
  { code: '13:00:00', label: '1:00 PM' },
  { code: '14:00:00', label: '2:00 PM' },
  { code: '15:00:00', label: '3:00 PM' },
  { code: '16:00:00', label: '4:00 PM' },
  { code: '17:00:00', label: '5:00 PM' },
  { code: '18:00:00', label: '6:00 PM' },
  { code: '19:00:00', label: '7:00 PM' },
  { code: '20:00:00', label: '8:00 PM' },
  { code: '21:00:00', label: '9:00 PM' },
  { code: '22:00:00', label: '10:00 PM' },
  { code: '23:00:00', label: '11:00 PM' },
];

export function MDMConfigModal({ open, onClose }) {
  const dispatch = useDispatch();
  useInjectReducer({
    key: MdmConfigSlice.sliceKey,
    reducer: MdmConfigSlice.reducer,
  });
  useInjectSaga({ key: MdmConfigSlice.sliceKey, saga: MdmConfigSaga });
  const initConfig = useSelector(MdmConfigSelector.selecMdmtConfig);
  const deviceConfigs: any = useSelector<any>(
    MdmConfigSelector.selecMdmtDeviceConfigs,
  );
  const appConfigs: any = useSelector<any>(
    MdmConfigSelector.selecMdmAppConfigs,
  );
  const { register, handleSubmit, errors, setValue } = useForm();
  const [versionId, setVersionId] = useState<any>(null);
  const [logTime, setLogTime] = useState<any>('07:30');

  useEffect(() => {
    if (_.isEmpty(initConfig)) {
      dispatch(MdmConfigSlice.actions.getMdmDeviceConfigAll(defaultDeviceName));
      dispatch(MdmConfigSlice.actions.getMdmAppConfigAll(defaultName));
    }
  }, [initConfig]);

  const onSubmit = config => {
    if (_.isEmpty(initConfig)) {
      dispatch(MdmConfigSlice.actions.createMdmConfig(config));
      onClose();
    }
    if (!_.isEmpty(initConfig)) {
      dispatch(MdmConfigSlice.actions.updateMdmConfig(config));
      onClose();
    }
  };

  useEffect(() => {
    function initFormData(config) {
      if (_.isEmpty(config)) return;
      for (let key in config) {
        if (key === 'log_time') {
          setLogTime(AppHelper.formLogTime(config[key]));
          setValue(key, AppHelper.formLogTime(config[key]));
        }
        setValue(key, config[key]);
      }
    }
    setTimeout(() => initFormData(initConfig));
    return () => {
      dispatch(MdmConfigSlice.actions.clearMdmConfig());
    };
  }, [initConfig]);
  const renderDeviceConfig = () => {
    if (!_.isEmpty(deviceConfigs) && deviceConfigs.data.length) {
      return deviceConfigs.data.map(({ id, name, code, ip }) => (
        <option value={id} key={id}>
          {name} | {code} | {ip}
        </option>
      ));
    }
  };

  const renderAppConfig = () => {
    if (!_.isEmpty(appConfigs) && appConfigs.data.length) {
      return appConfigs.data.map(({ id, name }) => (
        <option value={id} key={id}>
          {name}
        </option>
      ));
    }
  };

  const renderVersionId = () => {
    if (!_.isEmpty(appConfigs) && appConfigs.data.length) {
      const versionConfig = appConfigs.data.find(item => item.id === versionId);
      if (versionConfig) {
        return versionConfig.versions.map(column => {
          return (
            <option value={column.id} key={column.id}>
              {column.name}
            </option>
          );
        });
      }
    }
    if (
      !_.isEmpty(initConfig) &&
      !_.isEmpty(appConfigs) &&
      appConfigs.data.length
    ) {
      const versionConfig = appConfigs.data.find(
        item => item.id === initConfig.application_id,
      );
      if (versionConfig) {
        return versionConfig.versions.map(column => {
          if (column.id === initConfig.version_id) {
            return (
              <option value={column.id} key={column.id}>
                {column.name}
              </option>
            );
          }
          return (
            <option value={column.id} key={column.id}>
              {column.name}
            </option>
          );
        });
      }
    }
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3">
            {!_.isEmpty(initConfig) ? 'Cập Nhật Cài Đặt' : 'Thêm Cài Đặt'}
          </h4>
          <div className="form-group">
            <select
              className="form-control"
              name="device_id"
              ref={register({ required: true })}
            >
              {_.isEmpty(initConfig) ? (
                <option value="">Thiết Bị*</option>
              ) : null}
              {renderDeviceConfig()}
            </select>
            {errors.device_id && errors.device_id?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng Chọn Thiết Bị</span>
            ) : null}
          </div>
          <div className="form-group">
            <select
              className="form-control"
              name="application_id"
              ref={register({ required: true })}
              onChange={event => {
                setVersionId(event.currentTarget.value);
              }}
            >
              {_.isEmpty(initConfig) ? (
                <option value="">Ứng Dụng*</option>
              ) : null}
              {renderAppConfig()}
            </select>
            {errors.application_id &&
            errors.application_id?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng Chọn Ứng Dụng</span>
            ) : null}
          </div>
          <div className="form-row">
            <div className="col">
              <select
                className="form-control"
                name="version_id"
                id="version_id"
                ref={register({ required: true })}
              >
                {_.isEmpty(initConfig) ? (
                  <option value="">Chọn Phiên Bản</option>
                ) : null}
                {renderVersionId()}
              </select>
              {errors.version_id && errors.version_id?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>
                  Vui lòng Chọn Phiên Bản
                </span>
              ) : null}
            </div>
            <div className="col">
              <TextField
                id="time"
                type="time"
                size="small"
                fullWidth
                defaultValue="07:30"
                variant="outlined"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={{
                  step: 300,
                }}
                name="log_time"
                value={logTime}
                onChange={e => setLogTime(e.target.value)}
                inputRef={register({ required: true })}
              />
              {errors.log_time && errors.log_time?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>
                  Vui lòng Chọn Giờ LogTime
                </span>
              ) : null}
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="message">Thông Báo Cài Đặt</label>
            <textarea
              id="message"
              className="form-control message"
              name="message"
              ref={register({ required: true })}
              placeholder="Thông Báo"
              autoComplete="off"
            ></textarea>
            {errors.message && errors.message?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Tin Nhắn</span>
            ) : null}
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Đóng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lưu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
