/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from '../../core/types';
import { initialState } from './slice';

const selectSettingState = (state: RootState) =>
  state.mdmVersion || initialState;

export const selectError = createSelector(
  [selectSettingState],
  mdmVersionState => mdmVersionState.error,
);

export const selectSuccess = createSelector(
  [selectSettingState],
  mdmVersionState => mdmVersionState.success,
);

export const selectLoading = createSelector(
  [selectSettingState],
  mdmVersionState => mdmVersionState.loading,
);

export const selecMdmVersions = createSelector(
  [selectSettingState],
  mdmVersionState => mdmVersionState.versions,
);

export const selecMdmAppVersions = createSelector(
  [selectSettingState],
  mdmVersionState => mdmVersionState.appVersions,
);

export const selectMdmVersion = createSelector(
  [selectSettingState],
  mdmVersionState => mdmVersionState.version,
);
