import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { ConfigHttp } from '../services/mdmConfig.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';

const configRequest = new ConfigHttp();

export function* getMdmConfigAll(api, action) {
  const response = yield call(api.getMdmConfigAll, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmConfigAllSuccess(data));
  } catch (error) {
    yield put(actions.getMdmConfigAllFailure(_.get(error, 'message')));
  }
}

