export function* MdmConfigSaga() {
  yield all([
    yield takeLatest(
      actions.getMdmConfigAll.type,
      getMdmConfigAll,
      configRequest,
    ),
    yield takeLatest(
      actions.getMdmConfigById.type,
      getMdmConfigById,
      configRequest,
    ),
    yield takeLatest(
      actions.deleteMdmConfig.type,
      deleteMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.createMdmConfig.type,
      createMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.changeActiveAreaMdmConfig.type,
      changeActiveAreaMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.updateMdmConfig.type,
      updateMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.getMdmDeviceConfigAll.type,
      getMdmDeviceConfigAll,
      configRequest,
    ),
    yield takeLatest(
      actions.getMdmDeviceByid.type,
      getMdmDeviceByid,
      configRequest,
    ),
    yield takeLatest(
      actions.getMdmAppConfigAll.type,
      getMdmAppConfigAll,
      configRequest,
    ),
    yield takeLatest(actions.getMdmAppByid.type, getMdmAppByid, configRequest),
    yield takeLatest(
      actions.changeActiveMdmConfig.type,
      changeActiveMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.resetLogMdmConfig.type,
      resetLogMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.sendLogMdmConfig.type,
      sendLogMdmConfig,
      configRequest,
    ),
    yield takeLatest(
      actions.getLogMdmConfig.type,
      getLogMdmConfig,
      configRequest,
    ),
  ]);
}
