/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from '../../../store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_MDM_APP_CONSTANT,
  RESPONSE_MDM_APP_MESSAGE,
} from '../constants/mdmApp.constant';

export interface MdmAppState {
  loading?: boolean;
  success?: any;
  error?: any;
  mdmApps?: any;
  mdmApp: any;
}

export const initialState: MdmAppState = {
  loading: false,
  success: null,
  error: null,
  mdmApps: {},
  mdmApp: {},
};

const mdmAppFormSlice = createSlice({
  name: 'mdmApp',
  initialState,
  reducers: {
    getMdmAppAll(state, action) {
      state.loading = true;
    },

    getMdmAppAllSuccess(state, action) {
      state.mdmApps = action.payload;
      state.loading = false;
    },

    getMdmAppAllFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    getMdmAppById(state, action) {
      state.loading = true;
    },

    getMdmAppByIdSuccess(state, action) {      
      state.mdmApp = action.payload;
      state.loading = false;
    },

    getMdmAppByIdFailure(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    createMdmApp(state, action) {
      state.loading = true;
    },

    createMdmAppSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_APP_CONSTANT.CREATE_MDM_APP_SUCCESS,
        message: RESPONSE_MDM_APP_MESSAGE.CREATE_MDM_APP_SUCCESS,
      };
    },

    createMdmAppFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_APP_CONSTANT.CREATE_MDM_APP_FAIL,
        message: action.payload,
      };
    },

    updateMdmApp(state, action) {
      state.loading = true;
    },

    updateMdmAppSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_APP_CONSTANT.UPDATE_MDM_APP_SUCCESS,
        message: RESPONSE_MDM_APP_MESSAGE.UPDATE_MDM_APP_SUCCESS,
      };
    },

    updateMdmAppFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_APP_CONSTANT.UPDATE_MDM_APP_FAIL,
        message: RESPONSE_MDM_APP_MESSAGE.UPDATE_MDM_APP_FAIL,
      };
    },

    deleteMdmApp(state, action) {
      state.loading = true;
    },

    deleteMdmAppSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_APP_CONSTANT.DELETE_MDM_APP_SUCCESS,
        message: RESPONSE_MDM_APP_MESSAGE.DELETE_MDM_APP_SUCCESS,
      };
    },

    deleteMdmAppFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_MDM_APP_CONSTANT.DELETE_MDM_APP_FAIL,
        message: RESPONSE_MDM_APP_MESSAGE.DELETE_MDM_APP_FAIL,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearError(state) {
      state.error = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearMdmApp(state) {
      state.mdmApp = {};
    },

    resetAll(state) {
      state.loading = false;
      state.success = null;
      state.error = null;
      state.mdmApps = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = mdmAppFormSlice;
