import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { AppHttp } from '../services/mdmApp.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';

const appRequest = new AppHttp();
export function* getMdmAppAll(api, action) {
  const response = yield call(api.getMdmAppAll, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmAppAllSuccess(data));
  } catch (error) {
    yield put(actions.getMdmAppAllFailure(_.get(error, 'message')));
  }
}

export function* getMdmAppById(api, action) {
  const response = yield call(api.getMdmAppById, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMdmAppByIdSuccess(data));
  } catch (error) {
    yield put(actions.getMdmAppByIdFailure(_.get(error, 'message')));
  }
}

export function* createMdmApp(api, action) {
  const response = yield call(api.createMdmApp, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.createMdmAppSuccess(data));
  } catch (error) {
    yield put(actions.createMdmAppFailure(_.get(error, 'message')));
  }
}

export function* updateMdmApp(api, action) {
  const response = yield call(api.updateMdmApp, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.updateMdmAppSuccess(data));
  } catch (error) {
    yield put(actions.updateMdmAppFailure(_.get(error, 'message')));
  }
}

export function* deleteMdmApp(api, action) {
  const response = yield call(api.deleteMdmApp, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.deleteMdmAppSuccess(data));
  } catch (error) {
    yield put(actions.deleteMdmAppFailure(_.get(error, 'message')));
  }
}

export function* MdmAppSaga() {
  yield all([
    yield takeLatest(actions.getMdmAppAll.type, getMdmAppAll, appRequest),
    yield takeLatest(actions.getMdmAppById.type, getMdmAppById, appRequest),
    yield takeLatest(actions.createMdmApp.type, createMdmApp, appRequest),
    yield takeLatest(actions.deleteMdmApp.type, deleteMdmApp, appRequest),
    yield takeLatest(actions.updateMdmApp.type, updateMdmApp, appRequest),
  ]);
}
