/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect } from 'react';
import * as _ from 'lodash';
import * as MdmAppSlice from 'store/mdmApp/shared/slice';
import * as MdmAppSelector from 'store/mdmApp/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { MdmAppSaga } from 'store/mdmApp/shared/saga';
import { useForm } from 'react-hook-form';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

export function MDMModalApp({ open, onClose }) {
  useInjectReducer({
    key: MdmAppSlice.sliceKey,
    reducer: MdmAppSlice.reducer,
  });
  useInjectSaga({ key: MdmAppSlice.sliceKey, saga: MdmAppSaga });
  const initApp = useSelector(MdmAppSelector.selectMdmApp);
  const { register, handleSubmit, errors, setValue } = useForm();
  const dispatch = useDispatch();

  const onSubmit = app => {
    if (_.isEmpty(initApp)) {
      dispatch(MdmAppSlice.actions.createMdmApp(app));
      onClose();
    }
    if (!_.isEmpty(initApp)) {
      dispatch(MdmAppSlice.actions.updateMdmApp({ ...app, id: initApp.id }));
      onClose();
    }
  };

  useEffect(() => {
    function initFormData(app) {
      if (_.isEmpty(app)) return;
      for (let key in app) {
        setValue(key, app[key]);
      }
    }
    setTimeout(() => initFormData(initApp));
    return () => {
      dispatch(MdmAppSlice.actions.clearMdmApp());
    };
  }, [initApp]);

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3">
            {_.isEmpty(initApp) ? 'Thêm Mới Ứng Dụng' : 'Cập Nhật Ứng Dụng'}
          </h4>
          <div className="form-group">
            <label htmlFor="name">Tên Ứng Dụng</label>
            <input
              id="name"
              type="text"
              className="form-control name"
              name="name"
              ref={register({ required: true })}
              placeholder="Nhập Tên"
              autoComplete="off"
            />
            {errors.name && errors.name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lòng nhập Ứng Dụng</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="package_name">Tên Pagkage</label>
            <input
              id="package_name"
              type="text"
              className="form-control package_name"
              name="package_name"
              ref={register({ required: true })}
              placeholder="Mã"
              autoComplete="off"
            />
            {errors.package_name && errors.package_name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lòng nhập Tên Pagkage
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <input
              className="form-check-input ml-1 mr-2"
              type="checkbox"
              ref={register()}
              name="is_active"
            />
            <label
              className="form-check-label ml-2"
              htmlFor="is-actions"
              style={{ paddingLeft: '20px' }}
            >
              Hoạt dộng
            </label>
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Đóng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lưu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}