/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from '../../core/types';
import { initialState } from './slice';

const selectSettingState = (state: RootState) =>
  state.mdmDevice || initialState;

export const selectError = createSelector(
  [selectSettingState],
  mdmDeviceState => mdmDeviceState.error,
);

export const selectSuccess = createSelector(
  [selectSettingState],
  mdmDeviceState => mdmDeviceState.success,
);

export const selectLoading = createSelector(
  [selectSettingState],
  mdmDeviceState => mdmDeviceState.loading,
);

export const selecMdmtDevices = createSelector(
  [selectSettingState],
  mdmDeviceState => mdmDeviceState.mdmDevices,
);

export const selectMdmDevice = createSelector(
  [selectSettingState],
  mdmDeviceState => mdmDeviceState.mdmDevice,
);
