import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from '../../common/constants/common.constant';
import { MdmVersionApiRoutes } from '../constants/mdmVersion.constant';

export interface FilterVersion {
  take: number;
  skip: number;
}

export interface MdmVersionModel {
  id: string;
  name: string;
  code: number;
  isActive: boolean;
  applicationId: string;
}

export class VersionHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEVICE) {
    this.request = new HttpRequest(endPoint).request;
  }

  private configRequestBody = (mdmVersion: MdmVersionModel) => {
    let body = {
      Name: mdmVersion.name,
      Code: mdmVersion.code,
      IsActive: mdmVersion.isActive,
    };
    if (mdmVersion.id) {
      return {
        ...body,
        id: mdmVersion.id,
      };
    }
    return body;
  };

  public getMdmVersionAll = (filter: FilterVersion): Promise<any> => {
    return this.request.get(MdmVersionApiRoutes.GET_ALL_MDM_VERSION, filter);
  };

  public getMdmAppVersionAll = (filter: FilterVersion): Promise<any> => {
    return this.request.get(MdmVersionApiRoutes.GET_APP_ALL_VERSION, filter);
  };

  public getMdmVersionById = (id: string): Promise<any> => {
    return this.request.get(
      MdmVersionApiRoutes.GET_BY_ID_MDM_VERSION + '/' + id,
    );
  };

  public createMdmVersion = (fromData: FormData): Promise<any> => {
    return this.request.post(MdmVersionApiRoutes.CREATE_MDM_VERSION, fromData);
  };

  public updateMdmVersion = (mdmVersion: any): Promise<any> => {
    return this.request.post(MdmVersionApiRoutes.UPDATE_MDM_VERSION, {
      ...this.configRequestBody(mdmVersion),
    });
  };

  public deleteMdmVersion = (id: string): Promise<any> => {
    return this.request.post(MdmVersionApiRoutes.DELETE_MDM_VERSION + '/' + id);
  };
}
